import "core-js/modules/es.array.push.js";
import myEventBus from '@/assets/js/bus';
import { ref, toRefs, reactive, onMounted } from 'vue';
import { getdata } from '@/utils/request';
import { useRouter } from 'vue-router';
export default {
  name: 'HomeContent',
  setup(props, context) {
    const router = useRouter();
    const emitter = myEventBus();
    const data = reactive({
      showscrollimg: false,
      HomeContent: []
    });
    const gettodata = () => {
      getdata('/mock.json').then(res => {
        data.HomeContent = res.HomeContent;
      });
    };
    const tocontdetail = id => {
      router.push(`/jobsdetail/${id}`);
      window.scrollTo(0, 0);
    };
    const loading = ref(false);
    const onRefresh = () => {
      setTimeout(() => {
        emitter.emit('changegengxin', true);
        loading.value = false;
        const HomeContent = data.HomeContent;
        HomeContent.sort(() => {
          return 0.5 - Math.random();
        });
      }, 1000);
    };
    onMounted(() => {
      gettodata();
      emitter.on('checknums', checknum => {
        if (checknum !== 0) {
          data.showscrollimg = true;
          setTimeout(() => {
            data.showscrollimg = false;
            emitter.emit('changegengxin', true);
          }, 2000);
        }
        window.scrollTo(0, 0);
        setTimeout(() => {
          const HomeContent = data.HomeContent;
          HomeContent.sort(() => {
            return 0.5 - Math.random();
          });
        }, 1000);
      });
      emitter.on('scrollimg', () => {
        data.showscrollimg = true;
        setTimeout(() => {
          data.showscrollimg = false;
        }, 2000);
      });
    });
    return {
      ...toRefs(data),
      gettodata,
      tocontdetail,
      onRefresh,
      loading
    };
  }
};